@charset "UTF-8";

:root {
  --gatter: 15px;
  --gatter-lg: 15px;
  --gatter-md: 15px;
  --gatter-sm: 15px;
  --gatter-usm: 15px;
}

/* экран - HD */

.col-1 {
  width: calc(8.33% - var(--gatter));
}

.col-2 {
  width: calc(16.67% - var(--gatter));
}

.col-3 {
  width: calc(25% - var(--gatter));
}

.col-4 {
  width: calc(33.33% - var(--gatter));
}

