@charset "UTF-8";

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  display: block;
  max-width: 100%;
}

/* переменные*/

:root {
  --color-text1: #263238;
  --color--text2: #8f95a5;
  --color-black: #000;
  --color-hover: #eee;
  --color-hoverBlackButton: #ccc;
}

body {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
}

button,
a {
  cursor: pointer;
  transform: 0.3s all;
}

.contain {
  padding: 0 24px 0 30px;
  max-width: 1400px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: var(--color-text1);
}

.title-h2 {
  font-size: 48px;
  font-weight: 600;
  line-height: 80px;
  color: var(--color-black);
}

h3 {
  font-size: 30px;
  font-weight: 600;
  line-height: 48px;
  color: var(--color-text1);
}

.p-20 {
  max-width: 635px;
  font-size: 20px;
  line-height: 30px;
  color: var(--color--text2);
}

.p-16 {
  font-size: 16px;
  line-height: 24px;
  color: var(--color--text2);
}

/*Header*/

.header {
  padding-top: 50px;
}

.header__row-up {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logotype {
  font-size: 36px;
  line-height: 54px;
  margin-right: 18px;
}

.logotype span {
  color: #939393;
}

.header__menu {
  display: flex;
  font-size: 20px;
  font-weight: 600;
}

.header__menu__item {
  padding: 8px 15px;
  border-radius: 6px;
  margin-right: 10px;
  color: var(--color-text1);
  line-height: 30px;
}

.header__menu__item:hover {
  background-color: var(--color-hover);
}

.header__menu__item:last-child {
  margin-bottom: 0;
}

.button-white {
  margin-top: 6px;
  width: 220px;
  height: 70px;
  border: 2px solid #263238;
  border-radius: 6px;
  font-size: 24px;
  margin-right: 6px;
  background-color: #fff;
}

.button-white:hover {
  background-color: var(--color-hover);
}

.header__burger {
  display: none;
}

.header__button__img {
  display: none;
}

.header__content {
  padding-top: 50px;
  display: flex;
  justify-content: space-between;
}

.header__content__left {
  max-width: 632px;
}

.header__title {
  padding-top: 50px;
  display: flex;
  justify-content: start;
}

.header__title h1 {
  max-width: 400px;
  font-size: 64px;
  line-height: 80px;
  font-weight: 600;
}

.header__title img {
  margin-top: 92px;
  margin-left: 22px;
  max-width: 120px;
  max-height: 120px;
}

.header-h1a {
  display: none;
}

.header__content__left p {
  max-width: 540px;
  padding-top: 40px;
  font-size: 24px;
  line-height: 36px;
  color: var(--color--text2);
  margin-bottom: 62px;
}

.button-big {
  min-width: 438px;
  min-height: 118px;
  padding: 40px 118px;
  color: #fff;
  background-color: var(--color-text1);
  font-size: 36px;
  text-align: center;
  border: none;
  border-radius: 14px;
}

.header__content__right {
  max-width: 661px;
}

/*Skills*/

.skills {
  padding-top: 104px;
  margin-bottom: 104px;
}

.skills__wrapper {
  margin-right: 9px;
}

.skills__title {
  text-align: center;
  margin-bottom: 95px;
}

.skills__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-areas: "one two tree" "one four four";
  align-items: stretch;
  justify-content: space-evenly;
  column-gap: 18px;
  row-gap: 20px;
}

.skills__grid__item {
  padding: 23px 40px 40px 10px;
  background-color: #f5f5f5;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 434px;
}

.speaking {
  grid-area: one;
  justify-content: start;
}

.speaking__img {
  padding-top: 12px;
  padding-left: 10px;
  margin-bottom: 21px;
}

.speaking__content {
  padding-left: 30px;
}

.speaking__content p {
  max-width: 345px;
}

.speaking__content .p2 {
  max-width: 330px;
  padding-top: 24px;
}

.writing {
  grid-area: two;
}

.writing__img {
  margin-top: -27px;
  padding-left: 110px;
}

.writing__content {
  margin-top: -21px;
  padding-left: 30px;
}

.writing__content p {
  padding-top: 8px;
  max-width: 345px;
}

.reading {
  grid-area: tree;
}

.reading__img {
  margin-top: -15px;
  margin-left: -10px;
}

.reading__content {
  padding-top: 15px;
  padding-left: 30px;
}

.reading__content p {
  padding-top: 4px;
  max-width: 360px;
}

.listening {
  grid-area: four;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 885px;
}

.listening__img {
  margin-top: -15px;
  margin-left: -10px;
}

.listening__img img {
  padding-top: 42px;
  margin-right: 20px;
}

.listening__content {
  padding-top: 38px;
  padding-left: 36px;
}

.listening__content p {
  padding-top: 10px;
  max-width: 360px;
}

.listening__content button {
  margin-top: 30px;
}

.listening .listening__img {
  order: 1;
}

.button-common {
  font-weight: 500;
  font-size: 20px;
  line-height: 27px;
  color: #fff;
  background-color: var(--color-text1);
  border-radius: 5px;
  min-width: 220px;
  min-height: 60px;
  margin-top: 34px;
}

.button-common:hover {
  color: var(--color-hoverBlackButton);
}

/*About*/

.about {
  padding-top: 184px;
  margin-bottom: 180px;
}

.about__wrapper {
  display: flex;
  justify-content: space-between;
}

.about__title1 {
  display: none;
}

.about__title {
  margin-bottom: 23px;
}

.about__left p {
  margin-bottom: 38px;
}

.about__left__items {
  display: flex;
  justify-content: space-between;
  margin-right: 32px;
}

.about__left__item {
  margin-right: 0px;
}

.about__left__item h2 {
  text-align: center;
}

.about__left__item p {
  text-align: center;
  color: var(--color--text2);
}

.about__left__item :last-child {
  margin-right: 0;
}

.a-item1 {
  margin-right: 52px;
}

.about__right {
  margin-top: -32px;
  margin-right: 15px;
}

/*Pricing*/

.pricing {
  padding-bottom: 100px;
}

.pricing__title {
  padding-top: 9px;
  text-align: center;
  margin-bottom: 52px;
}

.pricing__items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 40px;
}

.pricing__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.up-1 h3 {
  padding-top: 42px;
}

.up-2 h3 {
  padding-top: 72px;
  padding-left: 14px;
}

.up-3 h3 {
  padding-top: 14px;
  padding-left: 20px;
}

.up-1 p {
  padding-top: 19px;
  margin-bottom: 29px;
}

.up-2 p {
  padding-top: 22px;
  padding-left: 16px;
}

.up-3 p {
  padding-left: 20px;
}

.pricing__item__lable {
  background-color: #eee;
  display: flex;
  align-items: center;
  justify-content: space-around;
  max-width: 240px;
  height: 45px;
  border-radius: 6px;
}

.lable__price {
  color: var(--color-text1);
  font-size: 30px;
  line-height: 45px;
}

.lable__text {
  color: #546269;
  font-size: 18px;
  line-height: 27px;
}

.price-l2 {
  margin-left: 16px;
}

.price-l3 {
  margin-left: 20px;
}

/*Contact*/

.contact {
  padding-top: 120px;
  padding-bottom: 100px;
}

.contact__wrapper {
  display: flex;
  justify-content: space-between;
}

.contact__img {
  max-width: 447px;
  max-height: 429px;
  margin-left: -18px;
  width: 100%;
  height: auto;
}

.contact__contain {
  padding-top: 32px;
}

.contact__title1 {
  display: none;
}

.contact__text {
  padding-top: 38px;
  max-width: 770px;
}

.contact__email {
  padding-top: 50px;
  display: flex;
}

.contact__email button {
  width: 264px;
  height: 80px;
  color: #fff;
  background-color: var(--color-text1);
  border-radius: 10px;
  font-size: 20px;
  line-height: 30px;
  font-weight: 500;
}

.contact__email button:hover {
  color: var(--color-hoverBlackButton);
}

input {
  width: 450px;
  height: 80px;
  padding-left: 32px;
  padding-top: 28px;
  padding-bottom: 30px;
  border-radius: 10px;
  border: 2px solid var(--color--text2);
  margin-right: 12px;
}

input::placeholder {
  font-size: 16px;
  line-height: 24px;
  color: var(--color--text2);
  letter-spacing: 0.8px;
}

input[type=email] {
  font-size: 24px;
  color: var(--color-text1);
}

/*Footer*/

.footer {
  padding: 58px 0;
}

.footer__line {
  margin: 0 auto;
  max-width: 1338px;
  width: 100%;
  height: 1px;
  background-color: #c4c4c4;
  margin-bottom: 31px;
}

.footer__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__items {
  display: flex;
}

.footer__item {
  font-size: 24px;
  line-height: 36px;
  color: var(--color--text2);
  cursor: pointer;
}

.footer__item a:hover {
  color: #939393;
}

.probel {
  font-size: 15px;
}

.symbol {
  font-family: Arial, sans-serif;
  font-size: 26px;
  color: #8F95A5;
}

